.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:rgb(7, 7, 34);
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 10px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 10px;
}

.card > .remove {
  color: #ff1744;
  font-size: 28px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  height: 30%;
  text-align: center;
  font-size: 3rem;
  font-family: 'Shadows Into Light', cursive;
  color: rgb(240, 242, 243);
  background-color: rgb(53, 36, 6);
  background-image: url("https://nilesdennard.files.wordpress.com/2017/09/hogwarts_pm_b4c37m1_greathallmourningcedricsdeath_moment.jpg");
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  padding-top: 50px;
  margin-top: 0%;
  margin-bottom: 50px;
}

.counter {
    width: 25%;
    height: 10%;
    float: left;
    text-align: center;
    font-size: 30px;
    background-image: url("http://www.africanoralhistory.com/imgs/bg.jpg");
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    padding-top: 15px;
    padding-bottom: 25px;
    margin-top: -5%;
    margin-bottom: 10px;
  }
* {
  box-sizing: border-box;
  font-family: sans-serif;

}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

